package com.example.pokerbanking.ui.home

import androidx.compose.foundation.ExperimentalFoundationApi
import androidx.compose.foundation.combinedClickable
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.layout.width
import androidx.compose.foundation.lazy.LazyColumn
import androidx.compose.foundation.lazy.items
import androidx.compose.material.icons.Icons
import androidx.compose.material.icons.filled.PlayArrow
import androidx.compose.material3.AlertDialog
import androidx.compose.material3.Button
import androidx.compose.material3.Divider
import androidx.compose.material3.ExperimentalMaterial3Api
import androidx.compose.material3.ExtendedFloatingActionButton
import androidx.compose.material3.Icon
import androidx.compose.material3.ListItem
import androidx.compose.material3.OutlinedButton
import androidx.compose.material3.OutlinedTextField
import androidx.compose.material3.Scaffold
import androidx.compose.material3.Text
import androidx.compose.material3.TextButton
import androidx.compose.material3.TopAppBar
import androidx.compose.runtime.Composable
import androidx.compose.runtime.collectAsState
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp

@OptIn(ExperimentalMaterial3Api::class, ExperimentalFoundationApi::class)
@Composable
fun HomeScreen(
    vm: HomeViewModel,
    onGameClick: (Long) -> Unit,
    onPlayersClick: () -> Unit
) {
    val games by vm.games.collectAsState()
    val gameToDelete by vm.gameToDelete.collectAsState()
    var showSetHostDialog by remember { mutableStateOf(false) }

    Scaffold(
        topBar = {
            TopAppBar(
                title = { Text("Poker Banking") },
                actions = {
                    TextButton(onClick = onPlayersClick) { Text("Players") }
                }
            )
        },
        floatingActionButton = {
            ExtendedFloatingActionButton(onClick = { showSetHostDialog = true }) {
                Icon(Icons.Default.PlayArrow, contentDescription = "Start Today")
                Spacer(Modifier.width(8.dp))
                Text("Start Today")
            }
        }
    ) { pad ->
        Column(
            Modifier.padding(pad).fillMaxSize().padding(12.dp)
        ) {
            if (games.isEmpty()) {
                Text("No games yet. Use 'Start Today' to create one.")
            } else {
                LazyColumn {
                    items(games) { game ->
                        ListItem(
                            headlineContent = { Text(game.date.toString()) },
                            supportingContent = { Text(if (game.isComplete) "Completed" else "Active") },
                            modifier = Modifier
                                .fillMaxWidth()
                                .combinedClickable(
                                    onClick = { onGameClick(game.id) },
                                    onLongClick = { vm.onDeleteGameClicked(game) }
                                )
                                .padding(vertical = 4.dp)
                        )
                        Divider()
                    }
                }
            }
        }
    }

    if (showSetHostDialog) {
        var hostName by remember { mutableStateOf("") }
        AlertDialog(
            onDismissRequest = { showSetHostDialog = false },
            title = { Text("Set Host") },
            text = { OutlinedTextField(value = hostName, onValueChange = { hostName = it }, label = { Text("Host Name") }) },
            confirmButton = {
                TextButton(onClick = {
                    vm.startTodayGame(hostName) { id -> onGameClick(id) }
                    showSetHostDialog = false
                }) { Text("Start") }
            },
            dismissButton = { TextButton(onClick = { showSetHostDialog = false }) { Text("Cancel") } }
        )
    }

    if (gameToDelete != null) {
        AlertDialog(
            onDismissRequest = { vm.onDeleteGameCancelled() },
            title = { Text("Delete Game") },
            text = { Text("Are you sure you want to delete the game from ${gameToDelete!!.date}? This action is irreversible.") },
            confirmButton = {
                Button(onClick = { vm.onDeleteGameConfirmed() }) {
                    Text("Delete")
                }
            },
            dismissButton = {
                OutlinedButton(onClick = { vm.onDeleteGameCancelled() }) {
                    Text("Cancel")
                }
            }
        )
    }
}
