package com.example.pokerbanking.ui.home

import com.example.pokerbanking.domain.Game
import com.example.pokerbanking.domain.GameRepository
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.launch
import kotlinx.datetime.Clock
import kotlinx.datetime.TimeZone
import kotlinx.datetime.todayIn
import moe.tlaster.precompose.viewmodel.ViewModel
import moe.tlaster.precompose.viewmodel.viewModelScope

class HomeViewModel(private val repo: GameRepository) : ViewModel() {
    private val _games = MutableStateFlow<List<Game>>(emptyList())
    val games: StateFlow<List<Game>> = _games.asStateFlow()

    private val _gameToDelete = MutableStateFlow<Game?>(null)
    val gameToDelete: StateFlow<Game?> = _gameToDelete.asStateFlow()

    fun loadGames() {
        viewModelScope.launch { repo.getGames().collect { _games.value = it } }
    }

    fun onDeleteGameClicked(game: Game) {
        _gameToDelete.value = game
    }

    fun onDeleteGameCancelled() {
        _gameToDelete.value = null
    }

    fun onDeleteGameConfirmed() {
        viewModelScope.launch {
            _gameToDelete.value?.let { repo.deleteGame(it.id) }
            _gameToDelete.value = null
        }
    }

    fun startTodayGame(hostName: String, onReady: (Long) -> Unit) {
        viewModelScope.launch {
            val today = Clock.System.todayIn(TimeZone.currentSystemDefault())
            val id = repo.createGameIfAbsent(today, hostName)
            onReady(id)
        }
    }
}
