import com.example.pokerbanking.domain.Game
import com.example.pokerbanking.domain.GameRepository
import com.example.pokerbanking.domain.SettlementTransaction
import kotlinx.coroutines.flow.Flow
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.map
import kotlinx.datetime.LocalDate

class FakeGameRepository : GameRepository {

    private val games = MutableStateFlow<List<Game>>(emptyList())
    private var nextGameId = 1L

    override fun getGames(): Flow<List<Game>> = games

    override fun getGame(gameId: Long): Flow<Game?> {
        return games.map { it.find { game -> game.id == gameId } }
    }

    override suspend fun createGameIfAbsent(date: LocalDate, hostName: String): Long {
        val existing = games.value.find { it.date == date }
        if (existing != null) {
            return existing.id
        }
        val newGame = Game(
            id = nextGameId++,
            date = date,
            isComplete = false,
            hostName = hostName
        )
        games.value += newGame
        return newGame.id
    }

    override suspend fun deleteGame(gameId: Long) {
        games.value = games.value.filterNot { it.id == gameId }
    }

