package com.example.pokerbanking.data

import com.example.pokerbanking.domain.Player
import com.example.pokerbanking.domain.PlayerRepository
import kotlinx.browser.localStorage
import kotlinx.coroutines.flow.Flow
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.serialization.encodeToString
import kotlinx.serialization.json.Json

class LocalStoragePlayerRepository : PlayerRepository {

    private val playersKey = "pokerbanking.players"

    private val playersFlow = MutableStateFlow<List<Player>>(emptyList())

    init {
        // Load initial players from localStorage
        val storedPlayers = localStorage.getItem(playersKey)
        if (storedPlayers != null) {
            try {
                playersFlow.value = Json.decodeFromString<List<Player>>(storedPlayers)
            } catch (e: Exception) {
                println("Error decoding players from localStorage: ${e.message}")
            }
        }
    }

    private fun savePlayers(players: List<Player>) {
        val jsonString = Json.encodeToString(players)
        localStorage.setItem(playersKey, jsonString)
        playersFlow.value = players
    }

    override fun getPlayers(): Flow<List<Player>> = playersFlow

    override suspend fun addPlayer(name: String, displayName: String, phoneNumber: String?, email: String?): Result<Long> {
        val currentPlayers = playersFlow.value
        if (currentPlayers.any { it.name.equals(name, ignoreCase = true) }) {
            return Result.failure(Exception("Player with this name already exists."))
        }
        val newId = (currentPlayers.maxOfOrNull { it.id } ?: 0L) + 1
        val newPlayer = Player(newId, name, displayName, phoneNumber, email)
        savePlayers(currentPlayers + newPlayer)
        return Result.success(newId)
    }

    override suspend fun addPlayer(name: String): Result<Long> {
        return addPlayer(name, name.split(" ").first(), null, null)
    }

    override suspend fun addPlayer(name: String, phoneNumber: String?, email: String?): Result<Long> {
        return addPlayer(name, name.split(" ").first(), phoneNumber, email)
    }

    override suspend fun updatePlayer(player: Player) {
        val currentPlayers = playersFlow.value
        val updatedList = currentPlayers.map { if (it.id == player.id) player else it }
        savePlayers(updatedList)
    }

    override suspend fun deletePlayer(player: Player) {
        val currentPlayers = playersFlow.value
        val updatedList = currentPlayers.filterNot { it.id == player.id }
        savePlayers(updatedList)
    }
}
