package com.example.pokerbanking.domain

import androidx.compose.runtime.Composable
import kotlinx.coroutines.await
import kotlin.js.Promise

// 1. Define simple, correct external interfaces for the JS APIs.
private external interface NavigatorWithContacts {
    val contacts: ContactsManager
}

private external interface ContactsManager {
    fun select(properties: Array<String>, options: ContactPickerOptions): Promise<Array<Contact>>
}

private external interface ContactPickerOptions {
    var multiple: Boolean
}

private external interface Contact {
    val name: Array<String>?
    val email: Array<String>?
    val tel: Array<String>?
}

// 2. The actual implementation returns a suspend function.
@Composable
actual fun rememberContactPicker(onContactSelected: (ContactInfo) -> Unit): suspend () -> Unit {
    return {
        try {
            // 3. Cast the global navigator object inside the suspend function.
            val navigator = js("navigator").unsafeCast<NavigatorWithContacts>()
            val options = js("{}").unsafeCast<ContactPickerOptions>()
            options.multiple = false

            // 4. Await the promise directly.
            val contacts = navigator.contacts.select(arrayOf("name", "email", "tel"), options).await()

            if (contacts.isNotEmpty()) {
                val contact = contacts[0]
                onContactSelected(
                    ContactInfo(
                        name = contact.name?.firstOrNull() ?: "",
                        phone = contact.tel?.firstOrNull(),
                        email = contact.email?.firstOrNull()
                    )
                )
            }
        } catch (e: dynamic) {
            println("Could not pick contact: ${e.message}")
        }
    }
}
