package com.example.pokerbanking.ui.playerlist

import com.example.pokerbanking.domain.Player
import com.example.pokerbanking.domain.PlayerRepository
import kotlinx.coroutines.flow.MutableSharedFlow
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.asSharedFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.launch
import moe.tlaster.precompose.viewmodel.ViewModel
import moe.tlaster.precompose.viewmodel.viewModelScope

class PlayerListViewModel(private val repo: PlayerRepository) : ViewModel() {
    private val _players = MutableStateFlow<List<Player>>(emptyList())
    val players: StateFlow<List<Player>> = _players.asStateFlow()

    private val _snackbarMessage = MutableSharedFlow<String>()
    val snackbarMessage = _snackbarMessage.asSharedFlow()

    init {
        viewModelScope.launch { repo.getPlayers().collect { _players.value = it } }
    }

    fun add(name: String, displayName: String, phone: String, email: String, onPlayerAdded: (Long) -> Unit) {
        if (name.isBlank()) return
        viewModelScope.launch {
            val result = repo.addPlayer(name, displayName, phone.ifBlank { null }, email.ifBlank { null })
            result.fold(
                onSuccess = {
                    _snackbarMessage.emit("Player $name added.")
                    onPlayerAdded(it)
                },
                onFailure = {
                    _snackbarMessage.emit(it.message ?: "Could not add player")
                }
            )
        }
    }

    fun update(player: Player) {
        viewModelScope.launch { repo.updatePlayer(player) }
    }

    fun delete(player: Player) {
        viewModelScope.launch { repo.deletePlayer(player) }
    }
}
