package com.example.pokerbanking

import androidx.compose.material3.MaterialTheme
import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.runtime.remember
import com.example.pokerbanking.data.FakeGameRepository
import com.example.pokerbanking.data.LocalStoragePlayerRepository
import com.example.pokerbanking.ui.home.HomeScreen
import com.example.pokerbanking.ui.home.HomeViewModel
import com.example.pokerbanking.ui.playerlist.PlayerListScreen
import com.example.pokerbanking.ui.playerlist.PlayerListViewModel
import moe.tlaster.precompose.PreComposeApp
import moe.tlaster.precompose.navigation.NavHost
import moe.tlaster.precompose.navigation.rememberNavigator
import moe.tlaster.precompose.viewmodel.viewModel


@Composable
fun App() {
    PreComposeApp {
        val navigator = rememberNavigator()
        // Create repositories that will be shared by all screens
        val gameRepository = remember { FakeGameRepository() }
        val playerRepository = remember { LocalStoragePlayerRepository() }

        MaterialTheme {
            NavHost(
                navigator = navigator,
                initialRoute = "/home"
            ) {
                scene("/home") {
                    val vm = viewModel { HomeViewModel(gameRepository) }
                    LaunchedEffect(Unit) { vm.loadGames() }

                    HomeScreen(
                        vm = vm,
                        onGameClick = { /* TODO */ },
                        onPlayersClick = { navigator.navigate("/players") }
                    )
                }
                scene("/players") {
                    val vm = viewModel { PlayerListViewModel(playerRepository) }
                    PlayerListScreen(
                        vm = vm,
                        onPlayerAdded = { /* TODO */ },
                        onBack = { navigator.goBack() }
                    )
                }
            }
        }
    }
}
